.instructor {
  color: #ffffff;
}
.instructor:focus .instructor__title {
  position: static;
  transform: none;
}
.instructor:focus .instructor__image-container {
  z-index: -1;
}
.instructor:hover .instructor__title {
  position: static;
  transform: none;
}
.instructor:hover .instructor__image-container {
  z-index: -1;
}
.instructor__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 294px;
  height: 100%;
  background: rgba(16, 38, 84, 0.89);
  padding: 54px 19%;
  padding-left: 17%;
  padding-bottom: 25px;
}
@media (max-width: 767px) {
  .instructor__wrapper {
    min-height: 274px;
    padding: 45px 10%;
    padding-bottom: 20px;
  }
}
.instructor__title {
  font-size: 18px;
  margin: 0 auto;
  position: absolute;
  bottom: 25px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 5;
}
.instructor__subtitle {
  margin: 0 auto;
  margin-bottom: 30px;
}
.instructor__advantages {
  margin: 0;
  padding-left: 25px;
  list-style-type: none;
}
.instructor__advantage {
  position: relative;
  font-size: 14px;
  margin: 0;
  margin-bottom: 8px;
}
.instructor__advantage::before {
  position: absolute;
  left: -17px;
  top: 0;
  content: "•";
  font-size: 170%;
}
.instructor__image-container {
  position: absolute;
  top: 0;
  left: 0;
